import { renderRecommendedProducts } from '../common/cart-drawer';

export const changeCartItemsCount = (count, countTotal) => {
  if (countTotal !== undefined) {
    document.querySelector(
      '.header__icon--cart .icon-title'
    ).textContent = `BAG (${countTotal})`;
  }
  if (count) {
    document.querySelector('.Header__CartDot').classList.add('is-visible');
  } else {
    document.querySelector('.Header__CartDot').classList.remove('is-visible');
  }
};

export const switchCheckoutUrlOnRerender = () => {
  const country = window.countryCode;
  if (!country) return;
  document
    .getElementById('CartDrawer-Checkout')
    ?.setAttribute('href', country === 'US' ? '/checkout' : '/cart');
};

export const fetchCartContent = async () => {
  const response = await fetch('/cart.js');
  if (response.ok) {
    return response.json();
  }
  return null;
};

export const rerenderCartContent = async () => {
  const isCartPage = window.location.pathname.includes('cart');
  let cartContent = await fetch(
    `/?section_id=${isCartPage ? 'main-cart-items' : 'cart-drawer'}`,
    {
      method: 'GET',
      credentials: 'same-origin',
      headers: { Accept: 'text/html' },
    }
  );

  cartContent = await cartContent.text();
  const mainItemsContainerSelector = isCartPage
    ? '.cart-page-items'
    : '#CartDrawer';
  const tempElement = document.createElement('div');

  tempElement.innerHTML = cartContent;
  const fetchedCartContent = tempElement.querySelector(
    mainItemsContainerSelector
  );

  if (!fetchedCartContent) return;

  const cartItems = fetchedCartContent.querySelectorAll('.cart__items--row');

  let itemsQuantityTotal = 0;
  cartItems.forEach((i) => {
    const quantity = i.querySelector('.quantity__input')?.value;
    if (!quantity) return;
    itemsQuantityTotal += +quantity;
  });
  const itemsCount = cartItems.length;

  changeCartItemsCount(itemsCount, itemsQuantityTotal);

  if (!itemsCount) return;

  const currentCartContent = document.querySelector(
    isCartPage ? '.main-cart-items-wrapper' : '.drawer'
  );
  currentCartContent.classList.remove('is-empty');
  currentCartContent.innerHTML = '';

  currentCartContent.insertAdjacentElement('beforeend', fetchedCartContent);

  if (!isCartPage) {
    renderRecommendedProducts();
  }
};

export const addItemsToCart = async (items = [], ...variants) => {
  try {
    if (!items.length) return {};
    const response = await fetch('/cart/add.js', {
      method: 'POST',
      mode: 'same-origin',
      cache: 'no-cache',
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json',
      },
      redirect: 'follow',
      referrerPolicy: 'no-referrer',
      body: JSON.stringify({ items }),
    }).then((res) => res.json());

    if (response.status?.toString()?.startsWith('4')) {
      return response;
    }

    await afterAddToCart(items, variants);
    return response;
  } catch (er) {
    console.log(er);
    return er;
  }
};

export const changeCartItems = async (quantityObj, items = [], ...variants) => {
  try {
    const fetchURL = `/cart/change.js`;
    const fetchObj = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(quantityObj),
    };
    const response = await fetch(fetchURL, fetchObj).then((res) => res.json());

    if (response.status?.toString()?.startsWith('4')) {
      return response;
    }
    await afterAddToCart(items, variants);
    return response;
  } catch (er) {
    console.log(er);
    return er;
  }
};

export const onAddToCart = async (items = [], ...variants) => {
  try {
    let response;
    // get the current cart data from Shopify
    const cartData = await fetch('/cart.js').then((res) => res.json());
    if (!cartData?.items?.length) {
      // check if cart is empty
      addItemsToCart(items, ...variants);
      return;
    }

    for (let item of items) {
      const itemIndex = cartData.items.findIndex(
        (cartItem) => item.id === cartItem.id
      );
      if (itemIndex >= 0) {
        // If the product is already in the cart, update the quantity instead of adding a new line item
        const quantityObj = {
          line: (itemIndex + 1).toString(), // id should be string according to the API documentation
          quantity: +item.quantity + +cartData.items[itemIndex].quantity,
        };
        response = await changeCartItems(quantityObj, items, ...variants);
      } else {
        // If the product isn't in the cart, add a new line item
        response = await addItemsToCart(items, ...variants);
      }
      return response;
    }
  } catch (er) {
    console.log(er);
  }
};

const afterAddToCart = async (items = [], ...variants) => {
  const event = {
    bubbles: true,
    detail: { variants, items },
  };

  await rerenderCartContent();
  switchCheckoutUrlOnRerender();
  document.dispatchEvent(new CustomEvent('cart:change', event));
};

export const sortCartBundleItems = (cartItems, cartItemsContainer) => {
  try {
    let cartItemsArr = Array.from(cartItems);
    if (!cartItemsArr.length) return;

    const bundleItems = [...cartItemsArr].filter((item, index) => {
      const isBundleItem =
        item.dataset.isDiscounted == 'true' &&
        item.dataset.isBundleItem == 'true';

      if (isBundleItem) {
        cartItemsArr = cartItemsArr.filter(
          (line) => line.dataset.index !== item.dataset.index
        );
      } else {
        item.classList.remove('cart-item--child');
      }

      return isBundleItem;
    });

    if (!bundleItems.length) return;

    const bundlesByParentId = {};
    bundleItems.forEach((item) => {
      const parentVariantId =
        item.dataset.bundleKey || item.dataset.parentBundleId;
      const maxBundleItemsCount = +item.dataset.bundleItemsCount;

      bundlesByParentId[parentVariantId] ||= {
        parentEl: null,
        items: [],
      };

      if (item.dataset.bundleKey) {
        bundlesByParentId[parentVariantId].parentEl = item;
      } else {
        if (
          maxBundleItemsCount > bundlesByParentId[parentVariantId].items.length
        ) {
          bundlesByParentId[parentVariantId].items.push(item);
        } else {
          cartItemsArr.push(item);
        }
      }
    });

    const virtualContainer = document.createElement('div');
    Object.values(bundlesByParentId).forEach((bundle) => {
      virtualContainer.appendChild(bundle.parentEl);
      bundle.items.forEach((el, index) => {
        virtualContainer.appendChild(el);
        if (index === bundle.items.length - 1) {
          el.classList.add('cart-item--last');
        }
      });
    });
    cartItemsArr.forEach((el) => virtualContainer.appendChild(el));
    cartItemsContainer.innerHTML = virtualContainer.innerHTML;
  } catch (er) {
    console.log('Error while sorting bundle items', er);
  }
};