import { retreiveProductMetafields } from '../services/products';
import { addItemsToCart, onAddToCart as onAddToCartService } from '../services/cart';
import { isFinalSale, retrieveSku, formatPrice, isUnreturnable } from '../utils/product';
import { replaceWords } from '../services/filter-value';

const tabletMedia = window.matchMedia('screen and (max-width: 1024px)');

const IMAGE_MEDIA = {
  // media: imageWidth
  'max-width: 400px': 340,
  'max-width: 1024px': 500,
  'min-width: 1200px': 900
};

const FEATURED_IMAGE_MEDIA = {
  'max-width: 400px': 600,
  'max-width: 1024px': 750,
  'min-width: 1200px': 1100
};

export const attachProductItemEvents = (renderedState, containerEl, isRebuy) => {
  if (!containerEl) return;
  const containerWrapper = containerEl.closest(`[id^="section-wrapper-"]`);
  const layout = containerWrapper?.dataset?.productCardLayout || "layout_2";
  Object.keys(renderedState).forEach(productId => {
    const productItems = Array.from(
      containerEl.querySelectorAll(`[data-id="${productId}"]`)
    );
    if (!productItems.length) return;
    renderedState[productId]['elements'] = productItems;
    productItems.forEach(itemEl => {
      const cartBtn = itemEl.querySelector('[data-add-to-cart-action]');
      const productItemBody = itemEl.querySelector('.product-item__body');
      const backdrop = itemEl.querySelectorAll('.product-item__backdrop');
      const options = Array.from(itemEl.querySelectorAll('[data-option]'));
      const dropdownItems = Array.from(itemEl.querySelectorAll('[data-value]'));
      cartBtn.onclick = () => onAddToCart(
        cartBtn, 
        productId, 
        renderedState, 
        isRebuy, 
        itemEl
      );
      dropdownItems.forEach(dropItem => (
        dropItem.onclick = () => onSelectOption(
          dropItem, 
          productId, 
          renderedState, 
          layout,
          itemEl
        )
      ));
      backdrop.forEach(b => b.addEventListener('click', () => {
        options.forEach(opt => opt.classList.remove('active'));
        b.classList.remove('active');
        productItemBody.classList.remove('active');
      }));
      if (layout !== 'layout_2') {
        options.forEach(opt => (
          [opt, opt.nextElementSibling].forEach(el => (
            el.onclick = () => {
              opt.classList.toggle('active');
              backdrop.forEach(b => b.classList.toggle('active'));
            }
          ))
        ));
      } else {
        itemEl.querySelector('.product-item__btn-toolbar-toggle')
          ?.addEventListener('click', () => {
            productItemBody.classList.toggle('active');
          });
      }
    });
  });
};

const createImageMediaList = (image, featuredCard) => {
  const mediaMap = featuredCard ? FEATURED_IMAGE_MEDIA : IMAGE_MEDIA;
  const mediaList = Object.entries(mediaMap).map(([media, imageWidth]) => (
    `
      <source 
        srcset="${window.resizeImage(image, imageWidth)}" 
        media="(${media})"
      >
    `
  ));
  return mediaList.join('\n');
};

